.intro {
  position: relative;
  height: 100vh;
  overflow: hidden;
}

.buttons--wrapper {
  position: absolute;
  bottom: 20%;
  left: 50%;
  transform: translateX(-50%);
}

.slider {
  position: relative;
}
.slide {
  min-width: 100vw;
  height: 60vh;
  position: absolute;
  background-color: white;
  left: 0;
  transition: left, transform 1s ease;
  z-index: 0;
}

.slide img{
  max-height: 60vh;
  position: absolute;
  transition:  transform 1s ease;
}


